import React from 'react';
// import { useParams } from 'react-router-dom';
import { Link } from 'react-router-dom';
import ActionButton from '../components/ActionButton';
import { MdAdd } from 'react-icons/md';
import LocaleContext from '../contexts/LocaleContext';
import LyricMusic from '../components/LyricMusic';
import { lyrics } from '../utils/lyrics';

function LyricPage() {
  // const { id } = useParams();
  // const path = window.location.pathname;

  const [isLoading, setLoading] = React.useState(true);
  const [ data, setData ] = React.useState('');
  const { idLyricContextValue } = React.useContext(LocaleContext);
  const { idLyric } = idLyricContextValue;

  React.useEffect(() => {
    const getData = (id) => {
      setLoading(true);
      const currentData = lyrics[id].lyric;
      setData(currentData);

      setTimeout(() => {
        setLoading(false);
      }, 800);
    }

    getData(idLyric);
  }, [idLyric]);

  return (
    <section>
      <LyricMusic isLoading={isLoading} lyric={data} />
      <Link to='/add'>
        <ActionButton icon={<MdAdd />} />
      </Link>
    </section>
  );
}

export default LyricPage;
